import { motion } from "framer-motion";
import { NavLink } from 'react-router-dom';
import { useSelector } from 'react-redux'
import IconButton from '@material-ui/core/IconButton';
import Badge from '@material-ui/core/Badge';
import ShoppingCartOutlinedIcon from '@material-ui/icons/ShoppingCartOutlined';

export default function Navbar() {
    const { items, user } = useSelector(state=>({
        items: state.cart.addedItems,
        user: state.currentUser
    }))

  return(
    <motion.nav className="navbar navbar-light container-fluid" 
        initial={{ y: '-100vh' }}
        animate={{ y: 0 }}
        transition={{ delay: 1.5 }}
    >   
        <ul className="navbar-nav">
            <li className="nav-item">
            <NavLink to='/' className="nav-link">
              <img src='https://kavaglam.s3.ca-central-1.amazonaws.com/kavaglam_logo_long.png' height='70px' alt="Kavaglam Logo" />
            </NavLink>
            </li>
        </ul>
        <div className="navbar-toggler" type="button" data-toggle="collapse" data-target="#collapsibleNavbar">
            <span className="navbar-toggler-icon"></span>
            <div className="collapse navbar-collapse justify-content-between" id="collapsibleNavbar" >
            <ul className="navbar-nav p-4" >
                <li className="nav-item">
                    <NavLink to='/' className="nav-link">Home</NavLink>
                </li>
                <li className="nav-item">
                    <NavLink to='/products' className="nav-link">Products</NavLink>
                </li>
                <li className="nav-item">
                    <NavLink to='/cart' className="nav-link">Cart</NavLink>
                </li>
                
                
                {user.admin &&  (
                    <li className="nav-item">
                        <NavLink to='/admin' className="nav-link">Admin</NavLink>
                    </li>
                )}
                <li className="nav-item">
                    <NavLink to='/cart' className="nav-link">
                        <IconButton aria-label="show cart" color="inherit">
                            <Badge badgeContent={items ? items.reduce((acc,i)=>{return acc+i.quantity}, 0) : null } color="secondary" />
                            <ShoppingCartOutlinedIcon />
                        </IconButton>
                    </NavLink>
                </li>
            </ul>
        </div>
        </div>
    </motion.nav>
  )
}


